import React, { useState } from 'react';

const AddPopup = ({ isOpen, onClose }) => {
  const [inputValue, setInputValue] = useState(''); // State to manage input value

  if (!isOpen) return null; // Do not render if the popup is not open

  const handleSave = () => {
    console.log('Input Value:', inputValue); // Replace with your save logic
    setInputValue(''); // Clear the input field after saving
    onClose(); // Close the popup
  };

  return (
    <div style={{
      position: 'fixed',
      top: '50%',
      left: '50%',
      transform: 'translate(-50%, -50%)',
      backgroundColor: '#fff',
      padding: '20px',
      borderRadius: '8px',
      boxShadow: '0 4px 8px rgba(0, 0, 0, 0.2)',
      zIndex: 1000,
    }}>
      <input
        type="text"
        value={inputValue} // Controlled input
        onChange={(e) => setInputValue(e.target.value)} // Update state on change
        style={{
          width: '90%',
          padding: '10px',
          marginBottom: '10px',
          borderRadius: '4px',
          border: '1px solid #ccc',
        }}
        placeholder="Enter shell name"
      />
      <div style={{ display: 'flex', justifyContent: 'space-between' }}>
        <button
          onClick={handleSave} // Save button logic
          style={{
            padding: '10px 20px',
            backgroundColor: '#4CAF50',
            color: '#fff',
            border: 'none',
            borderRadius: '4px',
            cursor: 'pointer',
          }}
        >
          Save
        </button>
        <button
          onClick={onClose} // Close button logic
          style={{
            padding: '10px 20px',
            backgroundColor: '#f44336',
            color: '#fff',
            border: 'none',
            borderRadius: '4px',
            cursor: 'pointer',
          }}
        >
          Close
        </button>
      </div>
    </div>
  );
};

export default AddPopup;