// src/libraries/LabelLibrary.js
import canvasEngine from '../canvas/CanvasEngine';
import Label from './Label';
const listeners = [];





class LabelLibrary {
  constructor() {
    this.labels = {}; // Structure: { layerId: [Label, Label, ...] }
  }

  // Add a new label
  addLabel(layerId, x, y, text = '', color = '#ffffff',fontType = 'monospace', rotation = 0,justification = 'centerleft') {
    const id = Date.now() + Math.random(); // Unique id
    const label = new Label({ id, text, x, y, layerId, color,fontType, rotation, justification });
    
    if (!this.labels[layerId]) {
      this.labels[layerId] = [];
    }
    this.labels[layerId].push(label);
    return label;
  }

  // Get all labels (flat array)
  getAllLabels() {
    return Object.values(this.labels)
      .flat()
      .filter(label => label && typeof label.text === 'string' && label.text.trim() !== '');
  }
  //shiv code
  getAllLabelsRaw() {
  return Object.values(this.labels).flat();
}
// end code

  

  // Delete a specific label by id
  deleteLabel(layerId, labelId) {
    if (!this.labels[layerId]) return;
    this.labels[layerId] = this.labels[layerId].filter(label => label.id !== labelId);
  }


  // Restore full label state from undo/redo
setAllLabels(newLabels) {
  this.labels = {};

  for (let label of newLabels) {
    if (!this.labels[label.layerId]) {
      this.labels[label.layerId] = [];
    }
    this.labels[label.layerId].push(new Label({ ...label }));
  }
}


getLabelById(labelId) {
  for (const layerId in this.labels) {
    const found = this.labels[layerId].find(label => label.id === labelId);
    if (found) return found;
  }
  return null;
}



// ✅ Toggle visibility of a single label
toggleLabelVisibility(labelId) {
  for (const layerId in this.labels) {
    const label = this.labels[layerId].find(l => l.id === labelId);
    if (label) {
      label.visible = !label.visible;
      return;
    }
  }
}


updateLabelText(labelId, newText) {
  for (const layerId in this.labels) {
    const label = this.labels[layerId].find(l => l.id === labelId);
    if (label) {
      label.text = newText;
      return;
    }
  }
}


// ✅ Toggle lock of a single label
toggleLabelLock(labelId) {
  for (const layerId in this.labels) {
    const label = this.labels[layerId].find(l => l.id === labelId);
    if (label) {
      label.locked = !label.locked;
      return;
    }
  }
}

// ✅ Toggle visibility for all labels
toggleAllVisibility(visibleState) {
  for (const layerId in this.labels) {
    this.labels[layerId].forEach(label => label.visible = visibleState);
  }
}

// ✅ Toggle lock for all labels
toggleAllLock(lockState) {
  for (const layerId in this.labels) {
    this.labels[layerId].forEach(label => label.locked = lockState);
  }
}

// ✅ Delete all labels
deleteAllLabels() {
  this.labels = {};
}



updateColorByLayer(layerId, newColor) {
  if (!this.labels[layerId]) return;
  this.labels[layerId].forEach(label => {
    label.color = newColor;
  });
}

updateLabelRotation(labelId, angle) {
  for (const layerId in this.labels) {
    const label = this.labels[layerId].find(l => l.id === labelId);
    if (label) {
      label.rotation = angle;
      
      console.log(`Rotation set to ${angle} for label ${labelId}`);
      return;
    }
  }
}

updateLabelFontType(labelId, fontType) {
  for (const layerId in this.labels) {
    const label = this.labels[layerId].find(l => l.id === labelId);
    if (label) {
      label.fontType = fontType;
      return;
    }
  }
}

updateLabelJustification(labelId, justification) {
  const validOptions = ['upperleft', 'upperright', 'lowerleft', 'lowerright', 'center', 'centerleft', 'centerright'];
  if (!validOptions.includes(justification)) {
    console.warn(`Invalid justification: ${justification}. Valid options are: ${validOptions.join(', ')}`);
    return;
  }

  for (const layerId in this.labels) {
    const label = this.labels[layerId].find(l => l.id === labelId);
    if (label) {
      label.justification = justification; // Update the justification
      console.log(`Justification set to ${justification} for label ${labelId}`);
      return;
    }
  }
}



  // Draw all labels
  drawAllLabels(ctx, offsetX, offsetY, scale, activeLayerId = null) {
    ctx.save();
    ctx.translate(offsetX, offsetY);
    ctx.scale(scale, scale);
  
    const allLabels = this.getAllLabelsRaw();// shiv code chnage getallLabels to getAllLabelsRaw
  
    for (let label of allLabels) {
      if (label.visible === false) continue; 
      ctx.save(); // Save per label
  
      ctx.fillStyle = label.color;
      ctx.font = `${label.fontSize}px ${label.fontType}`; // Use fontType from label
      
      // Set text alignment based on justification
      // 🔁 Full text alignment support
if (label.justification === 'upperleft') {
  ctx.textAlign = 'left';
  ctx.textBaseline = 'top';
} else if (label.justification === 'upperright') {
  ctx.textAlign = 'right';
  ctx.textBaseline = 'top';
} else if (label.justification === 'lowerleft') {
  ctx.textAlign = 'left';
  ctx.textBaseline = 'bottom';
} else if (label.justification === 'lowerright') {
  ctx.textAlign = 'right';
  ctx.textBaseline = 'bottom';
} else if (label.justification === 'center') {
  ctx.textAlign = 'center';
  ctx.textBaseline = 'middle';
} else if (label.justification === 'centerleft') {
  ctx.textAlign = 'left';
  ctx.textBaseline = 'middle';
} else if (label.justification === 'centerright') {
  ctx.textAlign = 'right';
  ctx.textBaseline = 'middle';
} else {
  ctx.textAlign = 'left';
  ctx.textBaseline = 'alphabetic'; // fallback
}



      // 1️⃣ Draw label text
      ctx.fillText(label.text, label.x, label.y);

      // shiv code
      // const textX = label.x;
      // const textY = label.y - label.fontSize / 2.3; // shift up by half height to vertically center
      // ctx.fillText(label.text, textX, textY);
      // end shiv code

  
      // 2️⃣ 🛠️ Draw small plus sign (+) after text draw
      ctx.restore(); // reset scaling
  
      ctx.save(); // Save context for plus sign
  
      // ctx.setTransform(1, 0, 0, 1, 0, 0); // Reset full transform
      ctx.translate(label.x, label.y); // manually position


      // shiv code
      // const textHeight = label.fontSize;
      // ctx.translate(label.x, label.y - textHeight / 2); // center-left position
      // end shiv code

      const plusSize = 5; // 5px size
      ctx.strokeStyle = "#00ff00";
      ctx.lineWidth = 1;
      ctx.beginPath();
      ctx.moveTo(-plusSize, 0);
      ctx.lineTo(plusSize, 0);
      ctx.moveTo(0, -plusSize);
      ctx.lineTo(0, plusSize);
      ctx.stroke();
  
      ctx.restore(); // Restore after plus sign
  
      ctx.save(); // Again save for selection box (important)
  
      // 3️⃣ 🔲 Draw dashed selection box if selected
      ctx.translate(offsetX, offsetY);
      ctx.scale(scale, scale);

      
  
      // if (label.selected) {
      //   ctx.fillStyle = label.color;
      //   ctx.font = `${label.fontSize}px ${label.fontType}`;
      //   const width = ctx.measureText(label.text).width;
      //   const height = label.fontSize;
      //   ctx.strokeStyle = "#ffffff";
      //   ctx.lineWidth = 2 / scale;
      //   ctx.setLineDash([4, 2]);
      //   ctx.strokeRect(label.x - 4, label.y - height - 2, width + 8, height + 8);
      //   ctx.setLineDash([]);
      // }
      if (label.selected) {
  const text = label.text;
  const fontSize = label.fontSize;
  const width = ctx.measureText(text).width;
  const height = fontSize;

  let boxX = label.x;
  let boxY = label.y;

  switch (label.justification) {
    case 'upperleft':
      // top-left corner — no adjustment needed
      break;
    case 'upperright':
      boxX -= width;
      break;
    case 'lowerleft':
      boxY -= height;
      break;
    case 'lowerright':
      boxX -= width;
      boxY -= height;
      break;
    case 'center':
      boxX -= width / 2;
      boxY -= height / 2;
      break;
    case 'centerleft':
      boxY -= height / 2;
      break;
    case 'centerright':
      boxX -= width;
      boxY -= height / 2;
      break;
    default:
      // fallback: treat as upperleft
      break;
  }

  ctx.strokeStyle = "#ffffff";
  ctx.lineWidth = 2 / scale;
  ctx.setLineDash([4, 2]);
  ctx.strokeRect(boxX - 4, boxY - 2, width + 8, height + 8);
  ctx.setLineDash([]);
}


  
      ctx.restore(); // Restore per label
    }
  
    ctx.restore();
  }
  

  // Clear all labels (optional future)
  clearAll() {
    this.labels = {};
  }
}

// Create singleton instance
const labelLibrary = new LabelLibrary();
export default labelLibrary;
