// instance section





drawInstanceTree(cell, ctx, parentTransform = { x: 0, y: 0, scale: 1, rotation: 0 }) {
  // Draw base elements in the cell
  cell.elements.forEach(element => {
      // Apply parent transform + element local transform
      const finalX = parentTransform.x + element.x * parentTransform.scale;
      const finalY = parentTransform.y + element.y * parentTransform.scale;

      ctx.save();
      ctx.translate(finalX, finalY);
      ctx.scale(parentTransform.scale, parentTransform.scale);
      ctx.rotate(parentTransform.rotation);

      // Draw rectangle or shape
      InstanceLibrary.drawSingle(ctx, element, parentTransform.scale);


      ctx.restore();
  });

  // Draw all instances recursively
  cell.instances.forEach(instance => {
      const childCell = window.layoutEngine.getCellByName(instance.ref);
      if (childCell) {
          const combinedTransform = {
              x: parentTransform.x + instance.x * parentTransform.scale,
              y: parentTransform.y + instance.y * parentTransform.scale,
              scale: parentTransform.scale * (instance.scale || 1),
              rotation: parentTransform.rotation + (instance.rotation || 0)
          };
          this.drawInstanceTree(childCell, ctx, combinedTransform);
      }
  });
}









