import React, { useState } from 'react';
import SearchIcon from '@mui/icons-material/Search';
import ExpandMoreIcon from '@mui/icons-material/ExpandMore';
import ChevronRightIcon from '@mui/icons-material/ChevronRight';

const HierarchyPanel = () => {
  const [searchTerm, setSearchTerm] = useState('');
  const [collapsedKeys, setCollapsedKeys] = useState({}); // Track collapsed/expanded state

  // Example dictionary object
  const dictionary = {
    Container1: {
      SubContainer1: {
        Subcontainer1_1: {
          Item4: 'Value1',
        },
        Item1: 'Value1',
        Item2: 'Value2',
      },
      SubContainer2: {
        Item3: 'Value3',
      },
    },
    Container2: {
      SubContainer3: {
        Item4: 'Value4',
      },
    },
    Container3: {
      SubContainer1: {
        Subcontainer1_1: {
          Item4: 'Value1',
        },
        Item1: 'Value1',
        Item2: 'Value2',
      },
      SubContainer2: {
        Item3: 'Value3',
      },
    },
  };

  // Toggle collapse/expand for a key
  const toggleCollapse = (key) => {
    setCollapsedKeys((prev) => ({
      ...prev,
      [key]: !prev[key],
    }));
  };

  // Recursive component to render the dictionary
  const renderDictionary = (obj, parentKey = '') => {
    return Object.keys(obj).map((key) => {
      const fullKey = parentKey ? `${parentKey}.${key}` : key; // Unique key for nested items
      const isCollapsed = collapsedKeys[fullKey];
      const value = obj[key];

      if (typeof value === 'object' && value !== null) {
        // Render nested dictionary
        return (
          <div key={fullKey} style={{ marginLeft: '0px', marginTop: '5px' }}>
            <div
              onClick={() => toggleCollapse(fullKey)}
              style={{
                cursor: 'pointer',
                display: 'flex',
                alignItems: 'center',
                color: '#00bcd4',
                fontSize: '12px',
                fontWeight: 'bold',
                padding: '5px',
                borderRadius: '4px',
                transition: 'background 0.2s',
              }}
              onMouseEnter={(e) => (e.target.style.background = '#1e1e1e')}
              onMouseLeave={(e) => (e.target.style.background = 'transparent')}
            >
              {isCollapsed ? (
                <ChevronRightIcon style={{ fontSize: '16px', marginRight: '5px' }} />
              ) : (
                <ExpandMoreIcon style={{ fontSize: '16px', marginRight: '5px' }} />
              )}
              {key}
            </div>
            {!isCollapsed && (
              <div style={{ marginLeft: '20px' }}>
                {renderDictionary(value, fullKey)}
              </div>
            )}
          </div>
        );
      }

      // Render key-value pair
      return (
        <div
          key={fullKey}
          style={{
            marginLeft: '0px',
            marginTop: '5px',
            display: 'flex',
            alignItems: 'center',
            padding: '5px',
            fontSize: '12px',
            borderRadius: '4px',
            transition: 'background 0.2s',
          }}
          onMouseEnter={(e) => (e.target.style.background = '#1e1e1e')}
          onMouseLeave={(e) => (e.target.style.background = 'transparent')}
        >
          <span style={{ color: '#fff', fontWeight: 'bold', marginRight: '5px' }}>
            {key}:
          </span>
          <span style={{ color: '#aaa' }}>{value}</span>
        </div>
      );
    });
  };

  // Filter dictionary based on search term
  const filterDictionary = (obj, term) => {
    if (!term) return obj;

    const filtered = {};
    Object.keys(obj).forEach((key) => {
      const value = obj[key];
      if (typeof value === 'object' && value !== null) {
        const nested = filterDictionary(value, term);
        if (Object.keys(nested).length > 0) {
          filtered[key] = nested;
        }
      } else if (key.toLowerCase().includes(term.toLowerCase())) {
        filtered[key] = value;
      }
    });
    return filtered;
  };

  const filteredDictionary = filterDictionary(dictionary, searchTerm);

  return (
    <div style={{ padding: '1px', color: '#fff', fontFamily: 'Arial, sans-serif' }}>
      {/* Search Bar */}
      <div
        style={{
          display: 'flex',
          alignItems: 'center',
          marginBottom: '12px',
          marginTop: '5px',
          background: '#2a2a2a',
          borderRadius: '6px',
          padding: '6px 10px',
          boxShadow: '0 2px 4px rgba(0, 0, 0, 0.2)',
        }}
      >
        <SearchIcon
          style={{ marginRight: '8px', fontSize: '20px', color: '#aaa' }}
        />
        <input
          type="text"
          placeholder="Search..."
          value={searchTerm}
          onChange={(e) => setSearchTerm(e.target.value)}
          style={{
            flexGrow: 1,
            background: 'transparent',
            width: '100%',
            height: '24px',
            border: 'none',
            outline: 'none',
            color: '#fff',
            fontSize: '14px',
          }}
        />
      </div>

      {/* Scrollable Container */}
      <div
        style={{
          maxHeight: '450px',
          width:'258px', // Set the maximum height for the scrollable container
          overflowY: 'auto', // Enable vertical scrolling
          background: '#1e1e1e',
          borderRadius: '6px',
          padding: '1px',
          boxShadow: '0 2px 4px rgba(0, 0, 0, 0.2)',
        }}
      >
        {renderDictionary(filteredDictionary)}
      </div>
    </div>
  );
};

export default HierarchyPanel;